import React, { type ReactNode, type InputHTMLAttributes } from 'react';
import classNames from 'classnames';
import './text-input.scss'; //   .module.scss  .scss

export type TextInputProps = {
  id?: string;
  value?: string | number;
  onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
  onKeyDown?: (event: React.KeyboardEvent<HTMLInputElement>) => void;
  disabled?: boolean;
  error?: string;
  placeholder?: string;
  label?: string;
  type?: string;
  name?: string;
  leadingIcon?: ReactNode;
  trailingIcon?: ReactNode;
  className?: string;
  style?: React.CSSProperties;
} & Omit<InputHTMLAttributes<HTMLInputElement>, 'onChange' | 'value' | 'prefix' | 'style' | 'className'>;

export function TextInput({
  id,
  value,
  onChange,
  onKeyDown,
  disabled,
  error,
  placeholder,
  label,
  type = 'text',
  name,
  leadingIcon,
  trailingIcon,
  className,
  style,
  ...rest
}: TextInputProps) {
  
  //   Class String  Prefix  styles object
  const rootClasses = classNames('lm-input-root', className);
  
  const containerClasses = classNames('lm-input-container', {
    'is-error': !!error,
    'is-disabled': disabled,
  });

  return (
    <div className={rootClasses} style={style}>
      {label && (
        <label htmlFor={id} className="lm-input-label">
          {label}
        </label>
      )}
      
      <div className={containerClasses}>
        {leadingIcon && (
          <div className="lm-input-leading-icon">
            {leadingIcon}
          </div>
        )}
        
        <input
          id={id}
          name={name}
          type={type}
          value={value}
          onChange={onChange}
          onKeyDown={onKeyDown}
          disabled={disabled}
          placeholder={placeholder}
          className="lm-input-field"
          aria-invalid={!!error}
          {...rest}
        />
        
        {trailingIcon && (
          <div className="lm-input-trailing-icon">
            {trailingIcon}
          </div>
        )}
      </div>

      {error && <span className="lm-input-error-message">{error}</span>}
    </div>
  );
}